import React from 'react';
import { Card, CircularProgress } from '@material-ui/core';
import CardBG from '../../assets/img/card_bg.jpg';
import {
    LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer,
} from 'recharts';
import moment from 'moment'

const CustomizedDot = (props) => {
    const { cx, cy } = props;

    return (
        <svg x={cx - 10} y={cy - 10} width={20} height={20} fill="green" viewBox="0 0 1024 1024">
            <text>ü¶Ä</text>
        </svg>
    );
};

const EcosystemChart = ({ loading, data, eco }) => {

    return (
        <Card className="card" elevation={6}>
            <img src={CardBG} width="100%" className="cardBG" alt="card background" />

            {loading && <CircularProgress/>}
            {!loading && (
                <ResponsiveContainer>
                    <LineChart data={data}>
                        {/* <CartesianGrid strokeDasharray="3 3" /> */}
                        {/* <XAxis dataKey="name" /> */}
                        {/* <XAxis dataKey={(d) => moment(d.totals.last_updated).format("H:mm a")} /> */}
                        <Tooltip />
                        <Legend />
                        {/* <Line type="monotone" name="Crabs" dataKey="crab.count" stroke="red" dot={<CustomizedDot />} />
                        <Line type="monotone" name="Crabs" dataKey="whale.count" stroke="red" dot={<CustomizedDot />} /> */}

                        {eco.map(e => <Line type="monotone" name={e.type} dataKey={(d) => d[e.key].count} stroke={e.color} dot={<CustomizedDot />}  /> )}
                    </LineChart>
                </ResponsiveContainer>
            )}
        </Card>
    )
}

export default EcosystemChart;